<template>
  <div class="login_wrap">
    <div class="container">
      <transition>
        <div class="alerts">
          <div ref="username">
            <b-alert
              style="color: #000"
              class="animate__animated animate__bounceInDown"
              :show="dismissCountDown"
              variant="danger"
              @dismissed="dismissCountDown = 0"
            >
              <b-icon icon="shield-lock-fill"></b-icon>
              用户名不能为空
            </b-alert>
          </div>
          <div ref="password">
            <b-alert
              style="color: #000"
              class="animate__animated animate__bounceInDown"
              :show="dismissCountDowns"
              variant="danger"
              @dismissed="dismissCountDowns = 0"
            >
              <b-icon icon="shield-lock-fill"></b-icon>
              密码不能为空
            </b-alert>
          </div>
          <div ref="usernames">
            <b-alert
              style="color: #000"
              class="animate__animated animate__bounceInDown"
              :show="dismissCountDowns1"
              variant="danger"
              @dismissed="dismissCountDowns1 = 0"
            >
              <b-icon icon="shield-lock-fill"></b-icon>
              用户名不存在
            </b-alert>
          </div>
          <div ref="passwords">
            <b-alert
              style="color: #000"
              class="animate__animated animate__bounceInDown"
              :show="dismissCountDownss"
              variant="danger"
              @dismissed="dismissCountDownss = 0"
            >
              <b-icon icon="shield-lock-fill"></b-icon>
              密码错误
            </b-alert>
          </div>
        </div>
      </transition>
      <div class="row">
        <div class="login col-lg-4">
          <p>欢迎登录 - MMALL管理系统</p>
          <b-form-input
            class="ipt"
            type="text"
            v-model.trim="username"
            placeholder="User Name"
          ></b-form-input>
          <b-form-input
            class="ipt"
            type="password"
            v-model.trim="password"
            placeholder="Password"
          ></b-form-input>
          <b-button class="but" block variant="primary" @click="login"
            >登录</b-button
          >
        </div>
      </div>
    </div>
  </div>
</template>


<script>
export default {
  data() {
    return {
      username: "admin",
      password: "admin",
      dismissSecs: 5,
      dismissCountDown: 0,
      dismissCountDowns: 0,
      dismissCountDownss: 0,
      dismissCountDowns1: 0,
    };
  },
  methods: {
    login() {
      if (this.username == "") {
        this.dismissCountDown = this.dismissSecs;
        this.$refs.password.style.display = "none";
        this.$refs.passwords.style.display = "none";
        this.$refs.usernames.style.display = "none";
        this.$refs.username.style.display = "block";
      } else if (this.password == "") {
        this.dismissCountDowns = this.dismissSecs;
        this.$refs.username.style.display = "none";
        this.$refs.usernames.style.display = "none";
        this.$refs.passwords.style.display = "none";
        this.$refs.password.style.display = "block";
      } else if (this.username != "admin") {
        this.dismissCountDowns1 = this.dismissSecs;
        this.$refs.username.style.display = "none";
        this.$refs.passwords.style.display = "none";
        this.$refs.password.style.display = "none";
        this.$refs.usernames.style.display = "block";
      } else if (this.password != "admin") {
        this.dismissCountDownss = this.dismissSecs;
        this.$refs.username.style.display = "none";
        this.$refs.usernames.style.display = "none";
        this.$refs.password.style.display = "none";
        this.$refs.passwords.style.display = "block";
      } else {
        this.$store.dispatch("login", {
          username: this.username,
          password: this.password,
        });
      }
    },
  },
  watch: {
    $route: {
      immediate: true,
      handler(val) {
        if (val.name == "Login") {
          document.title = "登录 - MMALL ADMIN";
        }
      },
    },
  },
};
</script>

<style scoped lang="scss">
.login_wrap {
  width: 100%;
  height: 100%;
  position: relative;
  background-image: url(../assets/login/TVYTbAXWheQpRcWDaDMu.svg);
  background-color: #000;
  .container {
    width: 100%;
    height: 100%;
    .alerts {
      width: 300px;
      position: absolute;
      top: 0;
      left: 50%;
      transform: translateX(-50%);
      height: 40px;
      .alert-dismissible .close {
        padding: 0.4rem 1.25rem !important;
      }
    }
    .row {
      width: 100%;
      height: 100%;
      .login {
        background-color: #fff;
        box-shadow: 1px 1px 4px 2px rgba(0, 0, 0, 0.3);
        width: 100%;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translateX(-50%) translateY(-50%);
        display: flex;
        flex-direction: column;
        padding: 25px;
        border-radius: 5px;
        p {
          font-size: 16px;
          color: #000;
          font-weight: 500;
        }
        .ipt {
          margin-bottom: 15px;
        }
      }
    }
  }
}
</style>
